"use client"

import { useRouter } from "next/navigation"
import { cn } from "@/lib/utils"
import { Button } from "@/components/ui/button"
import { Home, ShoppingBag, ListChecks, BarChart, Settings, Menu, Users } from 'lucide-react'

interface SidebarProps {
  isOpen: boolean
  toggleSidebar: () => void
}

// Componente personalizado para o ícone Queue
const Queue = () => (
  <svg
    xmlns="http://www.w3.org/2000/svg"
    width="24"
    height="24"
    viewBox="0 0 24 24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round"
  >
    <line x1="3" y1="6" x2="21" y2="6" />
    <line x1="3" y1="12" x2="21" y2="12" />
    <line x1="3" y1="18" x2="21" y2="18" />
  </svg>
)

const sidebarItems = [
  { href: "/dashboard", icon: Home, label: "Dashboard" },
  { href: "/vendas", icon: ShoppingBag, label: "Vendas" },
  { href: "/estoque", icon: ListChecks, label: "Estoque" },
  { href: "/fila", icon: Queue, label: "Fila de Clientes" },
  { href: "/relatorios", icon: BarChart, label: "Relatórios" },
  { href: "/usuarios", icon: Users, label: "Usuários" },
  { href: "/configuracoes", icon: Settings, label: "Configurações" },
]

export function Sidebar({ isOpen, toggleSidebar }: SidebarProps) {
  const router = useRouter()

  const handleNavigation = (href: string) => {
    router.push(href)
    // Não fechamos o sidebar após a navegação
  }

  return (
    <>
      {/* Mobile Sidebar Button */}
      <Button variant="ghost" size="icon" className="absolute left-4 top-4 z-50 md:hidden" onClick={toggleSidebar}>
        <Menu className="h-6 w-6" />
        <span className="sr-only">Abrir menu</span>
      </Button>

      {/* Sidebar */}
      <aside
        className={cn(
          "fixed left-0 top-0 z-40 flex h-full w-64 flex-col overflow-y-auto border-r bg-background py-4 transition-transform duration-300 ease-in-out md:translate-x-0",
          isOpen ? "translate-x-0" : "-translate-x-full",
        )}
      >
        <div className="px-6 pb-4">
          <h1 className="text-lg font-semibold">Sistema de Gestão</h1>
        </div>

        <nav className="flex-1 space-y-1 px-2">
          {sidebarItems.map((item) => (
            <Button
              key={item.href}
              variant="ghost"
              className="flex w-full items-center justify-start gap-2 rounded-md px-3.5 py-2 text-sm font-medium hover:bg-secondary"
              onClick={() => handleNavigation(item.href)}
            >
              <item.icon className="h-4 w-4" />
              <span>{item.label}</span>
            </Button>
          ))}
        </nav>
      </aside>
    </>
  )
}